import React, { Component } from 'react'
import Form from './Form'
import List from  './List'

export default class SimpleTodo extends Component {
  constructor(props){
    super(props)
    this.state = {
      todo: []
    }
    this.handleAdd = this.handleAdd.bind(this)
    this.handleRemove = this.handleRemove.bind(this)
  }

  // 
  handleAdd(e){
    e.preventDefault()
    // statetodo
    this.state.todo.push({title: e.target.title.value})
    // setStatestate
    this.setState({todo: this.state.todo})
    // inputvalue
    e.target.title.value = ''
  }

  // 
  handleRemove(i){
    // todoi1
    this.state.todo.splice(i,1)
    // setStatetodo
    this.setState({todo: this.state.todo})
  }

  render() {
    return (
      <div className="siimple-box siimple--bg-dark">
        <a href="https://mbp.hatenablog.com/entry/2021/07/10/211218" target="_blank" rel="noreferrer">MacReact</a><br />
        <a href="https://qiita.com/sayama0402/items/6364cef848e2c2d1cf7d" target="_blank" rel="noreferrer">ReactTodo</a>
        <h1 className="siimple-box-title siimple--color-white">Simple Todo App</h1>
        <Form handleAdd={this.handleAdd}/>
        <div className="siimple-rule"></div>
        <List todos={this.state.todo} handleRemove={this.handleRemove}/>
      </div>
    )
  }
}
