import React from "react";

class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = { title: "" };
  }

  render() {
    return (
      <div>
        <a className="QiitaApp-link" href="https://mbp.hatenablog.com/entry/2021/07/15/214300" target="_blank" rel="noreferrer">ReactQiita API(react-pages)</a><br />
        <a className="QiitaApp-link" href="https://qiita.com/kei_1011/items/cdc12086347025719152" target="_blank" rel="noreferrer">React Qiita API  - Qiita</a>
        <h3>QiitaReact</h3>
      </div>
    );
  }

  // value
  handleChange = (event) => {
    // value
    const title = event.target.value;
    // state
    this.setState({ title: title });
  };

  // submit
  handleSubmit = (event) => {
    event.preventDefault(); // 
    // app  state 
    this.props.search(this.state.title);
    this.setState({ title: "" }); // submittitle
  };
}

export default Search;
