import React from "react";

class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = { title: "" };
  }

  render() {
    return (
      <div>
        <a href="https://swiftintroduction.hatenablog.com/entry/2020/10/12/223935" target="_blank" rel="noreferrer">Stack OverflowAPIswift</a><br />
        <a href="https://qiita.com/kei_1011/items/cdc12086347025719152" target="_blank" rel="noreferrer">link</a>
        <h2>StackOverFlowreact</h2>
      </div>
    );
  }

  // value
  handleChange = (event) => {
    // value
    const title = event.target.value;
    // state
    this.setState({ title: title });
  };

  // submit
  handleSubmit = (event) => {
    event.preventDefault(); // 
    // app  state 
    this.props.search(this.state.title);
    this.setState({ title: "" }); // submittitle
  };
}

export default Search;
