import React from "react";

class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = { title: "" };
  }

  render() {
    return (
      <div>
        <a href="https://mbp.hatenablog.com/entry/2021/08/14/111848?_ga=2.196489130.788221064.1628304800-1619804006.1627465715" target="_blank" rel="noreferrer">AWS Lambda Hello WorldFeedly API</a><br />
        <a href="https://glasses-dog.hateblo.jp/search?q=feedly+Cloud" target="_blank" rel="noreferrer">feedly Cloud APIRSS - Glasses Dog</a><br />
        <a href="https://mbp.hatenablog.com/entry/2021/08/14/104812" target="_blank" rel="noreferrer">Feedly APICORS</a>
        <h2>Feedlyhbfav</h2>
      </div>
    );
  }

  // value
  handleChange = (event) => {
    // value
    const title = event.target.value;
    // state
    this.setState({ title: title });
  };

  // submit
  handleSubmit = (event) => {
    event.preventDefault(); // 
    // app  state 
    this.props.search(this.state.title);
    this.setState({ title: "" }); // submittitle
  };
}

export default Search;
