//  state useState  
import React, { useState } from 'react'
import './styles.css'

const Counter = () => {
  // count1~10
  const initialState = Math.floor(Math.random() * 10) + 1
  // count  state  initialState 
  const [count, setCount] = useState(initialState)
  // open  state  true 
  const [open, setOpen] = useState(true)
  // toggle
  const toggle = () => setOpen(!open)

  return (
    <>
        <a href="https://mbp.hatenablog.com/entry/2021/07/10/211218" target="_blank" rel="noreferrer">MacReact</a><br />
        <a href="https://qiita.com/seira/items/f063e262b1d57d7e78b4" target="_blank" rel="noreferrer">React hooks (useState)</a><br />
      <button className="siimple-btn siimple-btn--teal" onClick={toggle}>{open ? 'close' : 'open'}</button>
      <div className={open ? 'isOpen' : 'isClose'}>
        <p>{count}</p>
        {/* setCount()countcount */}
        <button className="siimple-btn siimple-btn--teal" onClick={() => setCount(prevState => prevState + 1)}>
          + 1
        </button>
        <button className="siimple-btn siimple-btn--teal" onClick={() => setCount(count - 1)}>- 1</button>
        <button className="siimple-btn siimple-btn--teal" onClick={() => setCount(0)}></button>
        <button className="siimple-btn siimple-btn--teal" onClick={() => setCount(initialState)}></button>
      </div>
    </>
  )
}

export default Counter
