import React from "react";

class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = { title: "" };
  }

  render() {
    return (
      <div>
        <a href="https://swiftintroduction.hatenablog.com/entry/2020/09/27/183044" target="_blank" rel="noreferrer">teratailAPIswift</a><br />
        <a href="https://qiita.com/_masa_u/items/4633519d6642098cd8fe" target="_blank" rel="noreferrer">null</a>
        <h2>Teratailreact.js</h2>
      </div>
    );
  }

  // value
  handleChange = (event) => {
    // value
    const title = event.target.value;
    // state
    this.setState({ title: title });
  };

  // submit
  handleSubmit = (event) => {
    event.preventDefault(); // 
    // app  state 
    this.props.search(this.state.title);
    this.setState({ title: "" }); // submittitle
  };
}

export default Search;
