import React from "react";

export default function TweetInput(props) {

  // 
  // ref.currentnull
  // JSXref
  const textareaRef = React.useRef(null);

  // Tweet
  // textareaRef.currentprops.addTweet
  // 
  const sendTweet = React.useCallback(() => {
    // refcurrentDOM
    // null
    // HTML
    if(textareaRef.current) {
      props.addTweet({
        id: new Date().getTime(), // ID
        icon: '', // 
        displayName: '',
        accountName: 'mrdeath',
        content: textareaRef.current.value
      });
    }
  }, [textareaRef.current, props.addTweet]);

  return (
    <div className="siimple-box siimple--bg-dark">
      <div><textarea className="tweet-textarea" ref={textareaRef}></textarea></div>
      <div><button onClick={sendTweet} className="siimple-btn siimple-btn--teal">Tweet</button></div>
    </div>
  );
}
