import React from "react";

class Search extends React.Component {
  constructor(props) {
    super(props);
    this.state = { title: "" };
  }

  render() {
    return (
      <div>
        <a href="https://mbp.hatenablog.com/entry/2021/08/09/152334" target="_blank" rel="noreferrer">Netlify FunctionsZenn Trend API</a><br />
        <a href="https://mbp.hatenablog.com/entry/2021/08/07/144120" target="_blank" rel="noreferrer">ZennAPI</a>
        <h2>ZennTech</h2>
        <a href="zennbookstrend">Books</a>
      </div>
    );
  }

  // value
  handleChange = (event) => {
    // value
    const title = event.target.value;
    // state
    this.setState({ title: title });
  };

  // submit
  handleSubmit = (event) => {
    event.preventDefault(); // 
    // app  state 
    this.props.search(this.state.title);
    this.setState({ title: "" }); // submittitle
  };
}

export default Search;
